import React, { useEffect, useState } from 'react';
import { Box, Typography, Button, Paper, TextField, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Snackbar, Alert, useMediaQuery, useTheme } from '@mui/material';
import { BACKEND_URL } from './config';

export default function Tournaments({ token }) {
  const [tournaments, setTournaments] = useState([]);
  const [profileRole, setProfileRole] = useState(null);
  const [showForm, setShowForm] = useState(false);
  const [editingTournament, setEditingTournament] = useState(null);
  const [form, setForm] = useState({ name: '', date: '', place: '', observations: '' });
  const [error, setError] = useState('');

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  useEffect(() => {
    if (token) {
      fetchTournaments();
      fetchProfileRole();
    }
  }, [token, showForm]);

  const fetchProfileRole = async () => {
    try {
      const res = await fetch(`${BACKEND_URL}/profile`, { headers: { 'Authorization': `Bearer ${token}` } });
      const data = await res.json();
      if (res.ok && data.user) setProfileRole(data.user.role);
    } catch {}
  };

  const fetchTournaments = async () => {
    setError('');
    try {
      const res = await fetch(`${BACKEND_URL}/tournaments`, { headers: { 'Authorization': `Bearer ${token}` } });
      const data = await res.json();
      if (!res.ok) setError(data.error || 'Error al cargar torneos');
      else setTournaments(data);
    } catch {
      setError('Error de conexin');
    }
  };

  const handleChange = e => setForm({ ...form, [e.target.name]: e.target.value });

  const handleCancel = () => {
    setShowForm(false);
    setEditingTournament(null);
    setForm({ name: '', date: '', place: '', observations: '' });
    setError('');
  };

  const handleEdit = (tournament) => {
    setEditingTournament(tournament);
    setForm({
      name: tournament.name,
      date: tournament.date,
      place: tournament.place || '',
      observations: tournament.observations || ''
    });
    setShowForm(true);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const url = editingTournament ? `${BACKEND_URL}/tournaments/${editingTournament.id}` : `${BACKEND_URL}/tournaments`;
      const method = editingTournament ? 'PUT' : 'POST';
      
      const res = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
        body: JSON.stringify(form)
      });
      const data = await res.json();
      if (!res.ok) setError(data.error || 'Error al guardar');
      else {
        setShowForm(false);
        setEditingTournament(null);
        setForm({ name: '', date: '', place: '', observations: '' });
        fetchTournaments();
      }
    } catch {
      setError('Error de conexin');
    }
  };

  return (
    <Box sx={{ maxWidth: { xs: '100%', sm: 700, md: 1000 }, width: '100%', margin: 'auto', p: 3 }}>
      <Typography variant="h5" align="center" gutterBottom>Registro de Torneos</Typography>
      {profileRole !== 'Club' && (
        <Button variant="contained" onClick={() => setShowForm(true)} sx={{ mb: 2, bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Registrar nuevo torneo</Button>
      )}
      {showForm && (
        <Paper sx={{ p: 2, mb: 3 }} elevation={2} component="form" onSubmit={handleSubmit}>
          <Typography variant="h6" gutterBottom>
            {editingTournament ? 'Editar Torneo' : 'Nuevo Torneo'}
          </Typography>
          <TextField name="name" label="Nombre" value={form.name} onChange={handleChange} fullWidth margin="normal" required />
          <TextField name="date" label="Fecha" type="date" value={form.date} onChange={handleChange} fullWidth margin="normal" InputLabelProps={{ shrink: true }} required />
          <TextField name="place" label="Lugar" value={form.place} onChange={handleChange} fullWidth margin="normal" />
          <TextField name="observations" label="Observaciones" value={form.observations} onChange={handleChange} fullWidth margin="normal" multiline rows={3} />
          <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
            <Button type="submit" variant="contained" fullWidth sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Guardar</Button>
            <Button type="button" variant="outlined" onClick={handleCancel} sx={{ color: '#901518', borderColor: '#901518', '&:hover': { bgcolor: '#901518', color: 'white' } }}>Cancelar</Button>
          </Box>
        </Paper>
      )}

      <TableContainer component={Paper} sx={{ mb: 3, width: '100%', overflowX: 'auto' }}>
        <Table sx={{ minWidth: isMobile ? 'auto' : 600 }}>
          <TableHead>
            <TableRow sx={{ backgroundColor: '#092B58' }}>
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Nombre</TableCell>
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Fecha</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Lugar</TableCell>}
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Detalles</TableCell>
              {profileRole !== 'Club' && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Acciones</TableCell>}
            </TableRow>
          </TableHead>
          <TableBody>
            {tournaments.map(t => (
              <TableRow key={t.id}>
                <TableCell sx={{ minWidth: isMobile ? 120 : 'auto' }}>
                  <Typography variant="body2" sx={{ fontWeight: 'medium' }}>
                    {t.name}
                  </Typography>
                  {isMobile && (
                    <Typography variant="caption" color="textSecondary" sx={{ display: 'block' }}>
                      {t.date}
                    </Typography>
                  )}
                </TableCell>
                {!isMobile && <TableCell>{t.date}</TableCell>}
                {!isMobile && <TableCell>{t.place}</TableCell>}
                <TableCell sx={{ minWidth: isMobile ? 140 : 'auto' }}>
                  {isMobile ? (
                    <Box>
                      <Typography variant="body2" sx={{ fontSize: '0.8rem' }}>
                        {t.place}
                      </Typography>
                      <Typography variant="caption" color="textSecondary" sx={{ 
                        display: 'block',
                        fontSize: '0.7rem',
                        mt: 0.5
                      }}>
                        {t.observations}
                      </Typography>
                    </Box>
                  ) : (
                    t.observations
                  )}
                </TableCell>
                {profileRole !== 'Club' && (
                  <TableCell>
                    <Button 
                      variant="outlined" 
                      size="small" 
                      onClick={() => handleEdit(t)}
                      sx={{ minWidth: 'auto' }}
                    >
                      Editar
                    </Button>
                  </TableCell>
                )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Snackbar open={!!error} autoHideDuration={4000} onClose={() => setError('')}>
        <Alert severity="error">{error}</Alert>
      </Snackbar>
    </Box>
  );
}
