import React, { useEffect, useState } from 'react';
import { BACKEND_URL } from './config';
import { Box, Typography, Button, TextField, Paper, MenuItem, Select, InputLabel, FormControl, Snackbar, Alert, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Card, CardContent, Grid, useTheme, useMediaQuery } from '@mui/material';

function Clubs({ token }) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('lg')); // Cambiado de 'md' a 'lg'
  const isTablet = useMediaQuery(theme.breakpoints.down('xl')); // Cambiado de 'lg' a 'xl'
  
  const [clubs, setClubs] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [profile, setProfile] = useState(null);
  const [editingId, setEditingId] = useState(null);
  const [form, setForm] = useState({
    name: '',
    legal_representative: '',
    status: 'activo',
    address: '',
    phone: '',
    contact_person: '',
    director_tecnico: '',
    fisioterapeuta: '',
    asistente_tecnico: '',
    delegado: ''
  });
  const [error, setError] = useState('');

  const fetchClubs = async () => {
    setError('');
    try {
    const res = await fetch(`${BACKEND_URL}/clubs`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (!res.ok) setError(data.error || 'Error al cargar clubes');
      else setClubs(data);
    } catch {
      setError('Error de conexin');
    }
  };

  useEffect(() => {
    fetchClubs();
  }, [showForm]);

  useEffect(() => {
    const fetchProfile = async () => {
      if (!token) return;
      try {
  const res = await fetch(`${BACKEND_URL}/profile`, { headers: { 'Authorization': `Bearer ${token}` } });
        const data = await res.json();
        if (res.ok) setProfile(data.user);
      } catch {}
    };
    fetchProfile();
  }, [token]);

  const handleChange = e => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setError('');
    try {
  const url = editingId ? `${BACKEND_URL}/clubs/${editingId}` : `${BACKEND_URL}/clubs`;
  const method = editingId ? 'PUT' : 'POST';
  const res = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(form)
      });
      const data = await res.json();
      if (!res.ok) setError(data.error || 'Error al registrar');
      else {
        setShowForm(false);
        setEditingId(null);
        setForm({ name: '', legal_representative: '', status: 'activo', address: '', phone: '', contact_person: '', director_tecnico: '', fisioterapeuta: '', asistente_tecnico: '', delegado: '' });
        fetchClubs();
      }
    } catch {
      setError('Error de conexin');
    }
  };

  const handleEdit = (club) => {
    setEditingId(club.id);
    setForm({
      name: club.name || '',
      legal_representative: club.legal_representative || '',
      status: club.status || 'activo',
      address: club.address || '',
      phone: club.phone || '',
      contact_person: club.contact_person || '',
      director_tecnico: club.director_tecnico || '',
      fisioterapeuta: club.fisioterapeuta || '',
      asistente_tecnico: club.asistente_tecnico || '',
      delegado: club.delegado || ''
    });
    setShowForm(true);
  };

  const handleCancel = () => {
    setShowForm(false);
    setEditingId(null);
    setForm({ name: '', legal_representative: '', status: 'activo', address: '', phone: '', contact_person: '', director_tecnico: '', fisioterapeuta: '', asistente_tecnico: '', delegado: '' });
  };

  return (
    <Box sx={{ 
      maxWidth: '100%', 
      width: '100%', 
      margin: 'auto', 
      p: { xs: 1, sm: 2, md: 3 },
      overflowX: 'hidden'
    }}>
      <Typography variant="h5" align="center" gutterBottom>Registro de Clubes</Typography>
      {!(profile && profile.role === 'Club') && (
        <Button variant="contained" onClick={() => setShowForm(true)} sx={{ mb: 2, bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Registrar nuevo club</Button>
      )}
      {showForm && (
        <Paper sx={{ p: 2, mb: 3 }} elevation={2}>
          <form onSubmit={handleSubmit}>
            <TextField name="name" label="Nombre del club" value={form.name} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="legal_representative" label="Representante legal" value={form.legal_representative} onChange={handleChange} required fullWidth margin="normal" />
            <FormControl fullWidth margin="normal">
              <InputLabel id="status-label">Estado</InputLabel>
              <Select name="status" labelId="status-label" value={form.status} label="Estado" onChange={handleChange} required>
                <MenuItem value="activo">Activo</MenuItem>
                <MenuItem value="inactivo">Inactivo</MenuItem>
              </Select>
            </FormControl>
            <TextField name="address" label="Direccin" value={form.address} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="phone" label="Telfono" value={form.phone} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="contact_person" label="Persona de contacto" value={form.contact_person} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="director_tecnico" label="Director tcnico" value={form.director_tecnico} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="fisioterapeuta" label="Fisioterapeuta" value={form.fisioterapeuta} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="asistente_tecnico" label="Asistente tcnico" value={form.asistente_tecnico} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="delegado" label="Delegado" value={form.delegado} onChange={handleChange} fullWidth margin="normal" />
            {editingId ? (
              <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
                <Button type="submit" variant="contained" fullWidth sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Guardar</Button>
                <Button type="button" variant="outlined" onClick={handleCancel} sx={{ color: '#901518', borderColor: '#901518', '&:hover': { bgcolor: '#901518', color: 'white' } }}>Cancelar</Button>
              </Box>
            ) : (
              <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
                <Button type="submit" variant="contained" fullWidth sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Guardar</Button>
                <Button type="button" variant="outlined" onClick={handleCancel} sx={{ color: '#901518', borderColor: '#901518', '&:hover': { bgcolor: '#901518', color: 'white' } }}>Cancelar</Button>
              </Box>
            )}
          </form>
        </Paper>
      )}
      <Snackbar open={!!error} autoHideDuration={4000} onClose={() => setError('')} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
        <Alert severity="error" onClose={() => setError('')}>{error}</Alert>
      </Snackbar>
      
      {isMobile ? (
        // Vista mvil: Cards
        <Grid container spacing={2}>
          {clubs.map(c => (
            <Grid item xs={12} key={c.id}>
              <Card elevation={2}>
                <CardContent>
                  <Typography variant="h6" component="div" gutterBottom>
                    {c.name}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    <strong>Representante:</strong> {c.legal_representative}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    <strong>Estado:</strong> {c.status}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    <strong>Telfono:</strong> {c.phone}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    <strong>Direccin:</strong> {c.address}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    <strong>Contacto:</strong> {c.contact_person}
                  </Typography>
                  {c.director_tecnico && (
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      <strong>Director tcnico:</strong> {c.director_tecnico}
                    </Typography>
                  )}
                  {!(profile && profile.role === 'Club') && (
                    <Box sx={{ mt: 2 }}>
                      <Button variant="outlined" size="small" onClick={() => handleEdit(c)} fullWidth>
                        Editar
                      </Button>
                    </Box>
                  )}
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      ) : (
        // Vista desktop y tablet: Tabla responsiva
        <TableContainer component={Paper} sx={{ width: '100%', overflowX: 'hidden' }}>
          <Table sx={{ width: '100%', tableLayout: 'fixed' }}>
            <TableHead>
              <TableRow sx={{ backgroundColor: '#092B58' }}>
                <TableCell sx={{ fontWeight: 'bold', width: isTablet ? '25%' : '18%', color: 'white' }}>Nombre</TableCell>
                <TableCell sx={{ fontWeight: 'bold', width: isTablet ? '25%' : '16%', color: 'white' }}>Representante</TableCell>
                <TableCell sx={{ fontWeight: 'bold', width: isTablet ? '15%' : '10%', color: 'white' }}>Estado</TableCell>
                <TableCell sx={{ fontWeight: 'bold', width: isTablet ? '20%' : '12%', color: 'white' }}>Telfono</TableCell>
                {!isTablet && (
                  <>
                    <TableCell sx={{ fontWeight: 'bold', width: '16%', color: 'white' }}>Direccin</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', width: '14%', color: 'white' }}>Contacto</TableCell>
                    <TableCell sx={{ fontWeight: 'bold', width: '14%', color: 'white' }}>Director</TableCell>
                  </>
                )}
                <TableCell sx={{ fontWeight: 'bold', width: isTablet ? '15%' : '10%', color: 'white' }}>Acciones</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {clubs.map(c => (
                <TableRow key={c.id}>
                  <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                    <Typography variant="body2" title={c.name}>
                      {c.name}
                    </Typography>
                  </TableCell>
                  <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                    <Typography variant="body2" title={c.legal_representative}>
                      {c.legal_representative}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography 
                      variant="body2" 
                      sx={{ 
                        color: c.status === 'activo' ? 'success.main' : 'error.main',
                        fontWeight: 'medium'
                      }}
                    >
                      {c.status}
                    </Typography>
                  </TableCell>
                  <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                    <Typography variant="body2" title={c.phone}>
                      {c.phone}
                    </Typography>
                  </TableCell>
                  {!isTablet && (
                    <>
                      <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                        <Typography variant="body2" title={c.address}>
                          {c.address}
                        </Typography>
                      </TableCell>
                      <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                        <Typography variant="body2" title={c.contact_person}>
                          {c.contact_person}
                        </Typography>
                      </TableCell>
                      <TableCell sx={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                        <Typography variant="body2" title={c.director_tecnico}>
                          {c.director_tecnico}
                        </Typography>
                      </TableCell>
                    </>
                  )}
                  <TableCell>
                    {!(profile && profile.role === 'Club') && (
                      <Button 
                        variant="outlined" 
                        size="small" 
                        onClick={() => handleEdit(c)}
                        sx={{ fontSize: '0.75rem', padding: '4px 8px' }}
                      >
                        Editar
                      </Button>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );
}

export default Clubs;
