import React, { useState } from 'react';
import Athletes from './Athletes';
import Clubs from './Clubs';
import Tournaments from './Tournaments';
import Users from './Users';
import { BACKEND_URL } from './config';
import { AppBar, Toolbar, IconButton, Typography, Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Button, TextField, Box, Container, Snackbar, Alert } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [token, setToken] = useState(() => localStorage.getItem('token') || '');
  const [profile, setProfile] = useState(null);
  const [menuOpen, setMenuOpen] = useState(false);
  const [view, setView] = useState('clubs');

  // Actualizar profile automticamente cuando el token cambie
  React.useEffect(() => {
    if (token) {
      getProfile();
    } else {
      setProfile(null);
    }
  }, [token]);
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
      try {
    const res = await fetch(`${BACKEND_URL}/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password })
      });
      const data = await res.json();
      if (!res.ok) {
        setError(data.error || 'Error de autenticacin');
      } else {
        setToken(data.token);
        localStorage.setItem('token', data.token);
        setView('clubs');
      }
    } catch (err) {
      setError('Error de conexin');
    }
  };

  const handleLogout = () => {
    setToken('');
    localStorage.removeItem('token');
  };
  const getProfile = async () => {
    setError('');
    try {
      const res = await fetch(`${BACKEND_URL}/profile`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (!res.ok) {
        setError(data.error || 'No autorizado');
      } else {
        setProfile(data.user);
      }
    } catch (err) {
      setError('Error de conexin');
    }
  };

    if (!token) {
      return (
        <Container maxWidth="xs">
          <Box sx={{ mt: 8, boxShadow: 3, p: 4, borderRadius: 2, bgcolor: 'background.paper' }}>
            {/* Logo de la Liga de Padel */}
            <Box sx={{ display: 'flex', justifyContent: 'center', mb: 3 }}>
              <img 
                src="/images/LOGO LIGA DE PADEL DE BOGOTA-03.jpg" 
                alt="Liga de Padel de Bogot" 
                style={{ 
                  maxWidth: '200px', 
                  width: '100%', 
                  height: 'auto',
                  borderRadius: '8px'
                }} 
              />
            </Box>
            <Typography variant="h5" align="center" gutterBottom>Login</Typography>
            <form onSubmit={handleSubmit}>
              <TextField
                label="Correo electrnico"
                type="email"
                value={email}
                onChange={e => setEmail(e.target.value)}
                required
                fullWidth
                margin="normal"
              />
              <TextField
                label="Contrasea"
                type="password"
                value={password}
                onChange={e => setPassword(e.target.value)}
                required
                fullWidth
                margin="normal"
              />
              <Button type="submit" variant="contained" fullWidth sx={{ mt: 2, bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Ingresar</Button>
            </form>
            <Snackbar open={!!error} autoHideDuration={4000} onClose={() => setError('')} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
              <Alert severity="error" onClose={() => setError('')}>{error}</Alert>
            </Snackbar>
          </Box>
        </Container>
      );
    }

    return (
      <Box sx={{ bgcolor: '#f5f5f5', minHeight: '100vh' }}>
        <AppBar position="static" sx={{ bgcolor: '#042653' }}>
          <Toolbar>
            <IconButton edge="start" color="inherit" aria-label="menu" onClick={() => setMenuOpen(true)}>
              <MenuIcon />
            </IconButton>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flexGrow: 1 }}>
              <img 
                src="/images/LOGO LIGA DE PADEL DE BOGOTA-03.jpg" 
                alt="Liga de Padel de Bogot" 
                style={{ 
                  height: '40px', 
                  width: 'auto',
                  marginRight: '12px',
                  borderRadius: '4px'
                }} 
              />
              <Typography variant="h6">
                Liga de Padel
              </Typography>
            </Box>
            <Button color="inherit" onClick={handleLogout}>Logout</Button>
          </Toolbar>
        </AppBar>
        <Drawer anchor="left" open={menuOpen} onClose={() => setMenuOpen(false)}>
          <List>
            <ListItem disablePadding>
              <ListItemButton onClick={() => { setView('athletes'); setMenuOpen(false); }}>
                <ListItemIcon><MenuIcon /></ListItemIcon>
                <ListItemText primary="Registro de deportistas" />
              </ListItemButton>
            </ListItem>
            <ListItem disablePadding>
              <ListItemButton onClick={() => { setView('clubs'); setMenuOpen(false); }}>
                <ListItemIcon><MenuIcon /></ListItemIcon>
                <ListItemText primary="Registro de clubes" />
              </ListItemButton>
            </ListItem>
            {/* Ocultar enlace de registro de torneos para Administrador */}
             {profile && profile.role === 'Administrador' && (
              <ListItem disablePadding>
                <ListItemButton onClick={() => { setView('tournaments'); setMenuOpen(false); }}>
                  <ListItemIcon><MenuIcon /></ListItemIcon>
                  <ListItemText primary="Registro de torneos" />
                </ListItemButton>
              </ListItem>
            )}
            {/* Solo para Administrador */}
            {profile && profile.role === 'Administrador' && (
              <ListItem disablePadding>
                <ListItemButton onClick={() => { setView('users'); setMenuOpen(false); }}>
                  <ListItemIcon><MenuIcon /></ListItemIcon>
                  <ListItemText primary="Alta de usuarios" />
                </ListItemButton>
              </ListItem>
            )}
            <ListItem disablePadding>
              <ListItemButton onClick={() => { setView('profile'); setMenuOpen(false); }}>
                <ListItemIcon><MenuIcon /></ListItemIcon>
                <ListItemText primary="Perfil" />
              </ListItemButton>
            </ListItem>
          </List>
        </Drawer>
        <Container maxWidth="md" sx={{ pt: 4 }}>
          {view === 'athletes' && <Athletes token={token} />}
          {view === 'clubs' && <Clubs token={token} />}
          {view === 'tournaments' && <Tournaments token={token} />}
          {view === 'users' && <Users token={token} userRole={profile?.role} />}
          {view === 'profile' && (
            <Box sx={{ maxWidth: 400, margin: 'auto', p: 3, bgcolor: 'background.paper', borderRadius: 2, boxShadow: 2 }}>
              <Typography variant="h5" align="center" gutterBottom>Bienvenido</Typography>
              {/* botn de perfil protegido eliminado: la informacin de perfil se muestra automticamente */}
              {profile && (
                <Box sx={{ mt: 2 }}>
                  <Typography><strong>Usuario:</strong> {profile.email}</Typography>
                  <Typography><strong>Role:</strong> {profile.role}</Typography>
                </Box>
              )}
              {error && <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>}
            </Box>
          )}
        </Container>
      </Box>
  );
}

export default Login;
