// Central frontend configuration
// Allows overriding the backend URL via environment variable REACT_APP_BACKEND_URL

// Detectar entorno de produccin
const isProduction = process.env.NODE_ENV === 'production';
const isBluehost = window.location.hostname !== 'localhost';

// Configuracin de URL del backend
let backendUrl;

if (process.env.REACT_APP_BACKEND_URL) {
  // Si est definida la variable de entorno, usarla
  backendUrl = process.env.REACT_APP_BACKEND_URL;
} else if (isProduction || isBluehost) {
  // En produccin, usar la URL relativa con el subdirectorio correcto
  backendUrl = '/LPBApp/api';
} else {
  // En desarrollo, usar localhost
  backendUrl = 'http://localhost:8080';
}

export const BACKEND_URL = backendUrl;

// Configuracin adicional para produccin
export const API_CONFIG = {
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  }
};

// Debug info (solo en desarrollo)
if (!isProduction) {
  console.log(' Backend URL:', backendUrl);
  console.log(' Environment:', process.env.NODE_ENV);
  console.log(' Hostname:', window.location.hostname);
}
