import React, { useEffect, useState } from 'react';
import { BACKEND_URL } from './config';
import { Box, Typography, Button, TextField, Paper, MenuItem, Select, InputLabel, FormControl, Snackbar, Alert, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TablePagination, Grid, Stack, IconButton, InputAdornment, Chip, useMediaQuery, useTheme } from '@mui/material';

function Athletes({ token }) {
  const [athletes, setAthletes] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [profile, setProfile] = useState(null);
  const [form, setForm] = useState({
    first_name: '',
    middle_name: '',
    last_name: '',
    second_last_name: '',
    email: '',
    birthdate: '',
    club_id: '',
    document_type: '',
    document_number: '',
    mobile_phone: ''
    ,rep_legal_name: '',
    rep_legal_email: '',
    rep_legal_phone: ''
  });
  const [error, setError] = useState('');
  const [clubs, setClubs] = useState([]);
  const [filterClub, setFilterClub] = useState('');
  const [filterName, setFilterName] = useState('');
  const [editingId, setEditingId] = useState(null);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const fetchAthletes = async () => {
    setError('');
    try {
      const res = await fetch(`${BACKEND_URL}/athletes`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (!res.ok) setError(data.error || 'Error al cargar deportistas');
      else setAthletes(Array.isArray(data) ? data : []);
    } catch {
      setError('Error de conexin');
      setAthletes([]);
    }
  };

  const fetchClubs = async () => {
    try {
      const res = await fetch(`${BACKEND_URL}/clubs`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (res.ok) {
        // If the logged-in user is a Club, show only their club in the combobox
        console.log('Fetched clubs:', data);
        console.log('Profile at fetchClubs:', profile);
        if (profile && profile.role === 'Club') {
          const own = data.find(c => c.id === profile.club_id);
          console.log('Own club found:', own);
          setClubs(own ? [own] : []);
        } else {
          setClubs(data);
        }
      }
    } catch {}
  };

  const fetchProfile = async () => {
    if (!token) return;
    try {
  const res = await fetch(`${BACKEND_URL}/profile`, { headers: { 'Authorization': `Bearer ${token}` } });
      const data = await res.json();
      if (res.ok) setProfile(data.user);
      console.log('Fetched profile:', data);
    } catch (e) {
      // ignore
    }
  };

  useEffect(() => {
    fetchProfile();
  }, [token]);

  useEffect(() => {
    fetchAthletes();
  }, [showForm, token]);

  useEffect(() => {
    // fetch clubs after we have profile (so we can filter for Club role)
    fetchClubs();
  }, [profile, showForm]);

  const clearFilters = () => {
    setFilterClub('');
    setFilterName('');
  };

  React.useEffect(() => {
    // reset to first page when filters change
    setPage(0);
  }, [filterClub, filterName]);

  const handleChange = e => {
    const next = { ...form, [e.target.name]: e.target.value };
    setForm(next);
    // If birthdate changed, we can compute age client-side to toggle rep fields visibility
    if (e.target.name === 'birthdate') {
      // nothing else here, rendering below will use computed age
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setError('');
    // client-side validation: club_id is required
    if (!form.club_id) {
      setError('El campo Club es obligatorio');
      return;
    }
    // compute age and if >18 ensure rep fields provided
    const computeAge = (bdate) => {
      if (!bdate) return null;
      const dob = new Date(bdate);
      if (isNaN(dob)) return null;
      const diff = Date.now() - dob.getTime();
      const ageDt = new Date(diff);
      return Math.abs(ageDt.getUTCFullYear() - 1970);
    };
    const age = computeAge(form.birthdate);
    if (age !== null && age < 18) {
      if (!form.rep_legal_name || !form.rep_legal_email || !form.rep_legal_phone) {
        setError('Deportista menor de 18 aos requiere datos de representante legal: nombre, email y celular');
        return;
      }
    }
    try {
  const url = editingId ? `${BACKEND_URL}/athletes/${editingId}` : `${BACKEND_URL}/athletes`;
      const method = editingId ? 'PUT' : 'POST';
      const res = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(form)
      });
      let data = {};
      try {
        data = await res.json();
      } catch (parseErr) {
        console.error('Error parsing JSON response from', url, parseErr);
      }
      if (!res.ok) {
        setError(data.error || data.message || `Error ${res.status}: ${res.statusText}`);
      } else {
        setShowForm(false);
        setEditingId(null);
        setForm({ first_name: '', middle_name: '', last_name: '', second_last_name: '', email: '', birthdate: '', club_id: '', document_type: '', document_number: '', mobile_phone: '', rep_legal_name: '', rep_legal_email: '', rep_legal_phone: '' });
        fetchAthletes();
      }
    } catch {
      setError('Error de conexin');
    }
  };

  const handleEdit = (athlete) => {
    setEditingId(athlete.id);
    setForm({
      first_name: athlete.first_name || '',
      middle_name: athlete.middle_name || '',
      last_name: athlete.last_name || '',
      second_last_name: athlete.second_last_name || '',
      email: athlete.email || '',
      birthdate: athlete.birthdate || '',
      club_id: athlete.club_id || '',
      document_type: athlete.document_type || '',
      document_number: athlete.document_number || '',
      mobile_phone: athlete.mobile_phone || ''
      ,rep_legal_name: athlete.rep_legal_name || '',
      rep_legal_email: athlete.rep_legal_email || '',
      rep_legal_phone: athlete.rep_legal_phone || ''
    });
    setShowForm(true);
  };

  const handleCancel = () => {
    setShowForm(false);
    setEditingId(null);
    setForm({ 
      first_name: '', 
      middle_name: '', 
      last_name: '', 
      second_last_name: '', 
      email: '', 
      birthdate: '', 
      club_id: '', 
      document_type: '', 
      document_number: '', 
      mobile_phone: '', 
      rep_legal_name: '', 
      rep_legal_email: '', 
      rep_legal_phone: '' 
    });
  };

  return (
    <Box sx={{ maxWidth: { xs: '100%', sm: 700, md: 1000 }, width: '100%', margin: 'auto', p: 3 }}>
      <Typography variant="h5" align="center" gutterBottom>Registro de Deportistas</Typography>
  <Box sx={{ mb: 2 }}>
    <Grid container spacing={2} alignItems="center">
      <Grid item xs={12}>
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <Button align="center" variant="contained" onClick={() => {
            setEditingId(null);
            // if logged user is Club, prefill club_id
            const initialClub = (profile && profile.role === 'Club') ? profile.club_id : '';
            setForm({ first_name: '', middle_name: '', last_name: '', second_last_name: '', email: '', birthdate: '', club_id: initialClub });
            setShowForm(true);
          }} sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Registrar nuevo deportista</Button>
        </Box>
      </Grid>
      <Grid item xs={12}>
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} alignItems="center">
          <FormControl size="small" sx={{ minWidth: 220, flex: 1 }}>
            <InputLabel id="filter-club-label">Filtrar por club</InputLabel>
            <Select labelId="filter-club-label" value={filterClub} label="Filtrar por club" onChange={e => setFilterClub(e.target.value)}>
              <MenuItem value=""><em>Todos los clubes</em></MenuItem>
              {clubs.map(c => (<MenuItem key={c.id} value={c.id}>{c.name}</MenuItem>))}
            </Select>
          </FormControl>
          <TextField
            size="small"
            label="Filtrar por nombre"
            value={filterName}
            onChange={e => setFilterName(e.target.value)}
            sx={{ minWidth: 240, flex: 1 }}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={() => setFilterName('')} edge="end" size="small"></IconButton>
                </InputAdornment>
              )
            }}
          />
          <Button variant="outlined" size="small" onClick={clearFilters}>Limpiar</Button>
        </Stack>
      </Grid>
    </Grid>
  </Box>
      {showForm && (
        <Paper sx={{ p: 2, mb: 3 }} elevation={2}>
          <form onSubmit={handleSubmit}>
            <TextField name="first_name" label="Primer nombre" value={form.first_name} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="middle_name" label="Segundo nombre" value={form.middle_name} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="last_name" label="Primer apellido" value={form.last_name} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="second_last_name" label="Segundo apellido" value={form.second_last_name} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="email" label="Correo" type="email" value={form.email} onChange={handleChange} required fullWidth margin="normal" />
            <FormControl fullWidth margin="normal">
              <InputLabel id="doc-type-label">Tipo de documento</InputLabel>
              <Select name="document_type" labelId="doc-type-label" value={form.document_type} label="Tipo de documento" onChange={handleChange} required>
                <MenuItem value="Cedula">Cdula</MenuItem>
                <MenuItem value="Tarjeta de Identidad">Tarjeta de Identidad</MenuItem>
                <MenuItem value="Registro Civil">Registro Civil</MenuItem>
                <MenuItem value="Cedula extranjeria">Cdula de extranjera</MenuItem>
                <MenuItem value="Pasaporte">Pasaporte</MenuItem>
              </Select>
            </FormControl>
            <TextField name="document_number" label="Nmero de documento" value={form.document_number} onChange={handleChange} required fullWidth margin="normal" />
            <TextField name="mobile_phone" label="Telfono celular" value={form.mobile_phone} onChange={handleChange} fullWidth margin="normal" />
            <TextField name="birthdate" label="Fecha de nacimiento" type="date" value={form.birthdate} onChange={handleChange} required fullWidth margin="normal" InputLabelProps={{ shrink: true }} />
            <FormControl fullWidth margin="normal">
              <InputLabel id="club-label">Club</InputLabel>
              <Select name="club_id" labelId="club-label" value={form.club_id} label="Club" onChange={handleChange} required>
                <MenuItem value=""><em>Selecciona un club</em></MenuItem>
                {clubs.map(c => (
                  <MenuItem key={c.id} value={c.id}>{c.name}</MenuItem>
                ))}
              </Select>
            </FormControl>
            {/* Representative fields for athletes older than 18 */}
            {(() => {
              if (!form.birthdate) return null;
              const dob = new Date(form.birthdate);
              if (isNaN(dob)) return null;
              const age = Math.floor((Date.now() - dob.getTime()) / (365.25 * 24 * 60 * 60 * 1000));
              if (age < 18) {
                return (
                  <>
                    <TextField name="rep_legal_name" label="Representante legal" value={form.rep_legal_name} onChange={handleChange} required fullWidth margin="normal" />
                    <TextField name="rep_legal_email" label="Correo del representante legal" type="email" value={form.rep_legal_email} onChange={handleChange} required fullWidth margin="normal" />
                    <TextField name="rep_legal_phone" label="Celular del representante legal" value={form.rep_legal_phone} onChange={handleChange} required fullWidth margin="normal" />
                  </>
                );
              }
              return null;
            })()}
            <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
              <Button type="submit" variant="contained" fullWidth sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Guardar</Button>
              <Button type="button" variant="outlined" onClick={handleCancel} sx={{ color: '#901518', borderColor: '#901518', '&:hover': { bgcolor: '#901518', color: 'white' } }}>Cancelar</Button>
            </Box>
          </form>
        </Paper>
      )}
      <Snackbar open={!!error} autoHideDuration={4000} onClose={() => setError('')} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
        <Alert severity="error" onClose={() => setError('')}>{error}</Alert>
      </Snackbar>
      <TableContainer component={Paper} sx={{ width: '100%', overflowX: 'auto' }}>
        <Table sx={{ minWidth: isMobile ? 'auto' : 650 }}>
          <TableHead>
            <TableRow sx={{ backgroundColor: '#092B58' }}>
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Nombre completo</TableCell>
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Tipo de documento</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Nmero de documento</TableCell>}
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Contacto</TableCell>
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Fecha de nacimiento</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Club</TableCell>}
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {(() => {
              const athletesList = Array.isArray(athletes) ? athletes : [];
              const filtered = athletesList.filter(a => {
                if (filterClub && String(a.club_id) !== String(filterClub)) return false;
                if (filterName) {
                  const full = `${a.first_name || ''} ${a.middle_name || ''} ${a.last_name || ''} ${a.second_last_name || ''}`.toLowerCase();
                  if (!full.includes(filterName.toLowerCase())) return false;
                }
                return true;
              });
              const visible = filtered.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);
              return visible.map(a => (
              <TableRow key={a.id}>
                    <TableCell sx={{ minWidth: isMobile ? 140 : 'auto' }}>
                      <Box>
                        <Typography variant="body2" sx={{ fontWeight: 'medium' }}>
                          {a.first_name} {a.middle_name} {a.last_name} {a.second_last_name}
                        </Typography>
                        {isMobile && (
                          <Box sx={{ mt: 0.5 }}>
                            <Chip 
                              label={a.document_type} 
                              size="small" 
                              sx={{ mr: 0.5, mb: 0.5, fontSize: '0.7rem' }}
                            />
                            <Typography variant="caption" color="textSecondary" sx={{ display: 'block' }}>
                              {a.document_number}
                            </Typography>
                          </Box>
                        )}
                      </Box>
                    </TableCell>
                    {!isMobile && <TableCell>{a.document_type}</TableCell>}
                    {!isMobile && <TableCell>{a.document_number}</TableCell>}
                    <TableCell sx={{ minWidth: isMobile ? 120 : 'auto' }}>
                      <Box>
                        <Typography variant="body2" sx={{ fontSize: isMobile ? '0.8rem' : 'inherit' }}>
                          {a.mobile_phone}
                        </Typography>
                        <Typography variant="caption" color="textSecondary" sx={{ 
                          display: 'block',
                          fontSize: isMobile ? '0.7rem' : 'inherit',
                          wordBreak: 'break-all'
                        }}>
                          {a.email}
                        </Typography>
                        {isMobile && (
                          <>
                            <Typography variant="caption" color="textSecondary" sx={{ display: 'block' }}>
                              {a.birthdate}
                            </Typography>
                            <Typography variant="caption" color="textSecondary" sx={{ display: 'block' }}>
                              {clubs.find(c => c.id === a.club_id)?.name || 'Sin club'}
                            </Typography>
                          </>
                        )}
                      </Box>
                    </TableCell>
                    {!isMobile && <TableCell>{a.birthdate}</TableCell>}
                    {!isMobile && <TableCell>{clubs.find(c => c.id === a.club_id)?.name || 'Sin club'}</TableCell>}
                    <TableCell>
                      <Button 
                        variant="outlined" 
                        size="small" 
                        onClick={() => handleEdit(a)} 
                        sx={{ 
                          mr: 1,
                          minWidth: isMobile ? 50 : 'auto',
                          fontSize: isMobile ? '0.7rem' : 'inherit'
                        }}
                      >
                        {isMobile ? 'Edit' : 'Editar'}
                      </Button>
                    </TableCell>
                  </TableRow>
              ));
            })()}
          </TableBody>
        </Table>
      </TableContainer>
        <TablePagination
          component="div"
          count={(Array.isArray(athletes) ? athletes : []).filter(a => {
            if (filterClub && String(a.club_id) !== String(filterClub)) return false;
            if (filterName) {
              const full = `${a.first_name || ''} ${a.middle_name || ''} ${a.last_name || ''} ${a.second_last_name || ''}`.toLowerCase();
              if (!full.includes(filterName.toLowerCase())) return false;
            }
            return true;
          }).length}
          page={page}
          onPageChange={(e, newPage) => setPage(newPage)}
          rowsPerPage={rowsPerPage}
          onRowsPerPageChange={e => { setRowsPerPage(parseInt(e.target.value, 10)); setPage(0); }}
          rowsPerPageOptions={[5, 10, 25, 50]}
          labelRowsPerPage="Filas por pgina"
          labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count !== -1 ? count : `ms de ${to}`}`}
        />
    </Box>
  );
}

export default Athletes;
