import React, { useEffect, useState } from 'react';
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Button, Divider, Snackbar, Typography, MenuItem, Select, FormControl, InputLabel, Chip, useMediaQuery, useTheme } from '@mui/material';
import { BACKEND_URL } from './config';
const roles = ['Administrador', 'Asistente', 'Club', 'Deportista'];
const estados = ['Activo', 'Inactivo'];

export default function Users({ token, userRole }) {
  const [users, setUsers] = useState([]);
  const [clubs, setClubs] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [editingUserId, setEditingUserId] = useState(null);
  // editForm removed; reuse `form` for both create and edit
  const [form, setForm] = useState({
    first_name: '',
    middle_name: '',
    last_name: '',
    second_last_name: '',
    email: '',
    password: '',
    birthdate: '',
    club_id: '',
    status: 'Activo',
    role: 'Deportista' // Valor por defecto vlido en lugar de cadena vaca
  });
  const [error, setError] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  useEffect(() => {
    if (token && userRole === 'Administrador') fetchUsers();
    // fetch clubs
    if (token) {
  fetch(`${BACKEND_URL}/clubs`, { headers: { 'Authorization': `Bearer ${token}` } })
        .then(r => r.json())
        .then(d => { if (Array.isArray(d)) setClubs(d); })
        .catch(() => {});
    }
  }, [token, showForm, userRole]);

  const fetchUsers = async () => {
    try {
      const res = await fetch(`${BACKEND_URL}/users`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (res.ok) setUsers(data);
      else setError(data.error || 'Error al cargar usuarios');
    } catch {
      setError('Error de conexin');
    }
  };

  const handleChange = e => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const startEdit = (user) => {
    setEditingUserId(user.id);
    // Validar que el rol del usuario est en la lista de roles vlidos
    const validRole = roles.includes(user.role) ? user.role : 'Deportista';
    // Asegurar que status tenga un valor vlido
    const validStatus = estados.includes(user.status) ? user.status : 'Activo';
    setForm({
      first_name: user.first_name || '',
      middle_name: user.middle_name || '',
      last_name: user.last_name || '',
      second_last_name: user.second_last_name || '',
      email: user.email || '',
      password: '',
      birthdate: user.birthdate || '',
      club_id: user.club_id || '',
      status: validStatus,
      role: validRole
    });
    setShowForm(true);
  };

  const handleCancel = () => {
    setEditingUserId(null);
    setShowForm(false);
    setForm({ 
      first_name: '', 
      middle_name: '', 
      last_name: '', 
      second_last_name: '', 
      email: '', 
      password: '', 
      birthdate: '', 
      club_id: '', 
      status: 'Activo',
      role: 'Deportista'
    });
  };

  // edit flow now uses the main form and handleSubmit

  const handleSubmit = async () => {
    setError('');
    const policy = /(?=.*[A-Z])(?=.*\d)(?=.*[^A-Za-z0-9]).{6,}/;
    // If creating (no editingUserId) password is mandatory and must satisfy policy
    if (!editingUserId) {
      if (!policy.test(form.password || '')) {
        setError('La contrasea debe tener al menos 6 caracteres, una mayscula, un nmero y un carcter especial');
        setSnackbarOpen(true);
        return;
      }
    } else {
      // If editing and password provided, validate
      if (form.password && !policy.test(form.password)) {
        setError('La contrasea debe tener al menos 6 caracteres, una mayscula, un nmero y un carcter especial');
        setSnackbarOpen(true);
        return;
      }
    }
    // Validar club para rol Club
    if (form.role === 'Club' && !form.club_id) {
      setError('Debe seleccionar un club para rol Club');
      setSnackbarOpen(true);
      return;
    }
    try {
      const url = editingUserId ? `${BACKEND_URL}/users/${editingUserId}` : `${BACKEND_URL}/users`;
      const method = editingUserId ? 'PUT' : 'POST';
      const payload = { ...form };
      if (editingUserId && (!payload.password || payload.password === '')) delete payload.password;

      const res = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });
      const data = await res.json();
      if (!res.ok) {
        setError(data.error || (editingUserId ? 'Error al editar' : 'Error al registrar'));
        setSnackbarOpen(true);
      } else {
        setShowForm(false);
        setEditingUserId(null);
        setForm({ 
          first_name: '', 
          middle_name: '', 
          last_name: '', 
          second_last_name: '', 
          email: '', 
          password: '', 
          birthdate: '', 
          club_id: '', 
          status: 'Activo', // Valor por defecto
          role: '' 
        });
        fetchUsers();
      }
    } catch {
      setError('Error de conexin');
      setSnackbarOpen(true);
    }
  };

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  if (userRole !== 'Administrador') {
    return <Typography variant="h6" color="error" align="center">Acceso solo para administrador</Typography>;
  }

  return (
    <Box sx={{ maxWidth: 900, margin: 'auto', p: 3 }}>
      <Typography variant="h5" align="center" gutterBottom>Alta de Usuarios</Typography>
      <Divider sx={{ mb: 2 }} />
      <Button variant="contained" onClick={() => setShowForm(true)} sx={{ mb: 2, bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Registrar nuevo usuario</Button>

      {showForm && (
        <Paper sx={{ p: 2, mb: 3 }} elevation={2}>
          <TextField label="Primer nombre" name="first_name" value={form.first_name} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
          <TextField label="Segundo nombre" name="middle_name" value={form.middle_name} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
          <TextField label="Primer apellido" name="last_name" value={form.last_name} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
          <TextField label="Segundo apellido" name="second_last_name" value={form.second_last_name} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
          <TextField label="Correo electrnico" name="email" value={form.email} onChange={handleChange} fullWidth sx={{ mb: 2 }} />
          <TextField label="Contrasea" name="password" value={form.password} onChange={handleChange} type="password" fullWidth sx={{ mb: 2 }} />
          <TextField label="Fecha de nacimiento" name="birthdate" value={form.birthdate} onChange={handleChange} type="date" InputLabelProps={{ shrink: true }} fullWidth sx={{ mb: 2 }} />
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Estado</InputLabel>
            <Select name="status" value={form.status} label="Estado" onChange={handleChange}>
              {estados.map(e => <MenuItem key={e} value={e}>{e}</MenuItem>)}
            </Select>
          </FormControl>
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Rol</InputLabel>
            <Select name="role" value={form.role} label="Rol" onChange={handleChange}>
              {roles.map(r => <MenuItem key={r} value={r}>{r}</MenuItem>)}
            </Select>
          </FormControl>
          {form.role === 'Club' && (
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel id="club-select-label">Club</InputLabel>
              <Select labelId="club-select-label" name="club_id" value={form.club_id} label="Club" onChange={handleChange}>
                <MenuItem value="">Seleccione un club</MenuItem>
                {clubs.map(c => <MenuItem key={c.id} value={c.id}>{c.name}</MenuItem>)}
              </Select>
            </FormControl>
          )}
          <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
            <Button variant="contained" onClick={handleSubmit} fullWidth sx={{ bgcolor: '#D0DC30', color: '#042653', '&:hover': { bgcolor: '#BCC929', color: '#042653' } }}>Guardar</Button>
            <Button type="button" variant="outlined" onClick={handleCancel} sx={{ color: '#901518', borderColor: '#901518', '&:hover': { bgcolor: '#901518', color: 'white' } }}>Cancelar</Button>
          </Box>
        </Paper>
      )}

      <TableContainer component={Paper} sx={{ mb: 3, overflowX: 'auto' }}>
        <Table sx={{ minWidth: isMobile ? 'auto' : 650 }}>
          <TableHead>
            <TableRow sx={{ backgroundColor: '#092B58' }}>
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Nombre</TableCell>
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Email</TableCell>
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Rol</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Estado</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Fecha de nacimiento</TableCell>}
              {!isMobile && <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Club</TableCell>}
              <TableCell sx={{ fontWeight: 'bold', color: 'white' }}>Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map(u => (
              <TableRow key={u.id}>
                <TableCell sx={{ minWidth: isMobile ? 120 : 'auto' }}>
                  <Box>
                    <Typography variant="body2" sx={{ fontWeight: 'medium' }}>
                      {`${u.first_name || ''} ${u.middle_name || ''} ${u.last_name || ''} ${u.second_last_name || ''}`}
                    </Typography>
                    {isMobile && (
                      <Box sx={{ mt: 0.5 }}>
                        <Chip label={u.role} size="small" sx={{ mr: 0.5, mb: 0.5 }} />
                        <Chip 
                          label={u.status} 
                          size="small" 
                          color={u.status === 'Activo' ? 'success' : 'default'}
                          sx={{ mr: 0.5, mb: 0.5 }}
                        />
                      </Box>
                    )}
                  </Box>
                </TableCell>
                <TableCell sx={{ minWidth: isMobile ? 140 : 'auto' }}>
                  <Box>
                    <Typography variant="body2">{u.email}</Typography>
                    {isMobile && u.birthdate && (
                      <Typography variant="caption" color="textSecondary">
                        {u.birthdate}
                      </Typography>
                    )}
                    {isMobile && u.role === 'Club' && (
                      <Typography variant="caption" color="textSecondary" sx={{ display: 'block' }}>
                        {clubs.find(c => Number(c.id) === Number(u.club_id))?.name || 'Club no encontrado'}
                      </Typography>
                    )}
                  </Box>
                </TableCell>
                {!isMobile && <TableCell>{u.role}</TableCell>}
                {!isMobile && (
                  <TableCell>
                    <Chip 
                      label={u.status} 
                      size="small" 
                      color={u.status === 'Activo' ? 'success' : 'default'}
                    />
                  </TableCell>
                )}
                {!isMobile && <TableCell>{u.birthdate}</TableCell>}
                {!isMobile && <TableCell>{(u.role === 'Club') ? (clubs.find(c => Number(c.id) === Number(u.club_id))?.name || 'Club no encontrado') : ''}</TableCell>}
                <TableCell>
                  <Button 
                    variant="outlined" 
                    size="small" 
                    onClick={() => startEdit(u)}
                    sx={{ minWidth: isMobile ? 60 : 'auto' }}
                  >
                    {isMobile ? 'Edit' : 'Editar'}
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Snackbar open={!!error || snackbarOpen} autoHideDuration={4000} onClose={() => { setSnackbarOpen(false); setError(''); }} message={error} />
    </Box>
  );
}
